<template>
	<div class="app-container" v-loading="isLoad" >
		<p style="overflow: hidden;">
			<el-col :span="4">
				<div class="item-left">选择角色:</div>
			</el-col>
			<el-col :span="12">
				<el-select v-model="role.value" filterable placeholder="请选择" @change="companyChange" style="width: 100%;">
					<el-option v-for="item in role.options" :key="item.mid?item.mid:item.ctId" :label="item.mname?item.mname:item.ctName" :value="item.mid?item.mid:item.ctId">
					</el-option>
				</el-select>
			</el-col>
		</p>
		<div style="padding: 10px 80px;overflow: hidden;">
			<el-card class="flow-item" :body-style="{ padding: '0px' }" v-for="(item, index) in flows" :key="index">
				<h3 style="font-weight: 500;padding-top:20px;font-size: 24px;color: #666;margin: 0;">套餐{{index+1}}</h3>
				<p class="price">￥{{item.ptPrice}}</p>
				<div class="flow-year">{{item.ptId}}</div>
				<div class="flow-name">{{item.ptName}}</div>
				<el-button type="warning" style="width: 80%;margin-bottom: 20px;" @click="openConfirm(item)">编辑价格</el-button>
			</el-card>
		</div>
	</div>
</template>

<script>
/* eslint-disable quotes,indent,keyword-spacing,spaced-comment,no-var */
import {
	giveManufacturerAuthority,
	getListManufacturerGeneral,
	getListCTInfoGeneral
} from '../../../api/admin/usual';
import {
	
	getListManufacturerPackage
} from '../../../api/admin/basicData';
export default {
	data() {
		return {
			role: {
				value: '',
				options: [],
			},
			cur_mid: 0,
			flows: [],
			isLoad: true
		}
	},
	watch: {},
	mounted() {

		this.$nextTick(() => {
			getListManufacturerGeneral()
				.then(res => {
					if (res.msg == 0) {
						this.role.value = res.mList[0].mid;
						this.role.options = res.mList;
						this.cur_mid = res.mList[0].mid;
						this.ListManufacturerPackage(this.cur_mid);
					}
				})
		});
	},
	methods: {
		openConfirm(item) {
			this.$prompt('设置价格', '设置价格', {
				confirmButtonText: '确定',
				cancelButtonText: '取消',
				inputValue: item.ptPrice,
				inputPattern: /^[+-]?([0-9]*\.?[0-9]+|[0-9]+\.?[0-9]*)([eE][+-]?[0-9]+)?$/,
				inputErrorMessage: '只能输入正数和浮点数'
			}).then(({
					value
				}) => {
				this.$message({
					type: 'success',
					message: '价格修改: ' + item.ptPrice + ' => ' + value
				});
				item.ptPrice = value;
			}).catch(() => {
				this.$message({
					type: 'info',
					message: '取消修改'
				});
			});
		},
		companyChange(val) {
			console.log("val", val)
			this.ListManufacturerPackage(val);
		},
		ListManufacturerPackage(mid) {
			this.isLoad=true
			giveManufacturerAuthority({
				mid: mid
			})
				.then(res => {
					this.isLoad=false
					if (res.msg == 0) {
						getListManufacturerPackage()
							.then(res => {
								if (res.msg == 0) {
									this.flows = res.packageList;
								}
							})
					}
				})
		}
	}
};
</script>

<style scoped>
.app-container {
	background: #f5f5f5;
}

.flow-item {
	float: left;
	width: 300px;
	margin-top: 10px;
	text-align: center;
}

.flow-item:nth-child(n+1) {
	margin: 10px 10px 0 10px;
}

.flow-item .price {
	font-size: 40px;
	font-weight: bold;
	color: #666;
	margin: 0;
}

.flow-year {
	color: #aaa;
	font-size: 13px;
}

.flow-name {
	width: 80%;
	margin: 20px auto 15px;
	padding: 15px 0 0 15px;
	border-top: 1px solid #e5ded6;
	text-align: left;
	color: #9ea7b3;
}
</style>


